.basket {
    padding: 95px 260px 130px 260px;
    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        &-title {
            display: flex;
            align-items: center;
            font-style: normal;
            font-weight: 700;
            font-size: 32px;
            line-height: 39px;
            img {
                margin-right: 17px;
            }
        }
        &-clearBasketButton {
            display: flex;
            align-items: center;
            width: 169px;
            height: 23px;
            border: none;
            background-color: #fff;
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #B6B6B6;
            cursor: pointer;
            &:hover {
                color: #f53030;
            }
            img {
                margin: 0 7px 5px 0;
            }
        }
    }
    &__item {
        margin-top: 30px;
        border-top: 1px solid #F7F7F7;
        &-wrapper {
            padding-top: 30px;
            display: grid;
            grid-template-columns: 350px 102px 150px 32px;
            justify-content: space-between;
            align-items: center;
        }
        &-pizza {
            display: grid;
            grid-template-columns: 80px 240px;
            align-items: center;
            column-gap: 15px;
            img {
                width: 80px;
                height: 80px;
            }
        }
        &-title {
            font-style: normal;
            font-weight: 700;
            font-size: 22px;
            line-height: 27px;
            letter-spacing: 0.01em;
        }
        &-type {
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            line-height: 22px;
            letter-spacing: 0.01em;
            color: #8D8D8D;
        }
        &-amount {
            width: 102px;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &-signButton {
            width: 32px;
            height: 32px;
            display: flex;
            justify-content: center;
            align-items: center;
            background: #FFFFFF;
            border: 2px solid #FE5F1E;
            border-radius: 100%;
            cursor: pointer;
            &:hover {
                background: #EB5A1E;
                color: #FFFFFF;
                svg path {
                    fill: #FFFFFF;
                }
            }
            svg {
                width: 9.6px;
            }
        }
        &-number {
            font-style: normal;
            font-weight: 700;
            font-size: 22px;
            line-height: 27px;
            letter-spacing: 0.01em;
        }
        &-price {
            text-align: right;
            font-style: normal;
            font-weight: 700;
            font-size: 22px;
            line-height: 27px;
            letter-spacing: 0.01em;
        }
        &-remove {
            width: 32px;
            height: 32px;
            left: 1139px;
            top: 399px;
            background: #FFFFFF;
            border: 2px solid #D7D7D7;
            border-radius: 100%;
            cursor: pointer;
            &:hover {
                background: #282828;
            }
            img {
                width: 10.6px;
            }
        }
    }
    &__total {
        display: flex;
        justify-content: space-between;
        margin-top: 40px;
        &-amountOfPizzas {
            font-style: normal;
            font-weight: 400;
            font-size: 22px;
            line-height: 27px;
            letter-spacing: 0.01em;
            span {
                font-weight: 700;  
            }
        }
        &-totalPrice {
            font-style: normal;
            font-weight: 400;
            font-size: 22px;
            line-height: 27px;
            letter-spacing: 0.01em;
            span {
                font-weight: 700;
                color:#FE5F1E;
            }
        }
    }
    &__buttons {
        display: flex;
        justify-content: space-between;
        margin-top: 40px;
        &-back {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 211px;
            height: 55px;
            border: 1px solid #D3D3D3;
            background: #FFFFFF;
            border-radius: 30px;
            
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #CACACA;
            text-decoration: none;
            cursor: pointer;
            &:hover {
                background: #282828;
            } 
            img {
                margin-right: 10px;
            }
        }
        &-pay {
            width: 211px;
            height: 55px;
            background: #FE5F1E;
            border-radius: 30px;
            border: none;

            font-style: normal;
            font-weight: 700;
            font-size: 16px;
            line-height: 19px;
            text-align: center;
            color: #FFFFFF;
            cursor: pointer;
        }
    }

}

.emptyBasket {
    padding: 163px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__title {
        font-style: normal;
        font-weight: 700;
        font-size: 32px;
        line-height: 39px;
        letter-spacing: 0.01em;
    }
    &__prompt {
        margin-top: 10px;
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 26px;
        text-align: center;
        letter-spacing: 0.01em;
        color: #777777;
    }
    &__img {
        width: 300px;
        height: 255px;
        margin-top: 47px;
    }
    &__backButton {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 74px;
        width: 210px;
        height: 46px;
        background: #282828;
        border-radius: 30px;
        border: none;

        text-decoration: none;
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        letter-spacing: 0.015em;
        color: #FFFFFF;
        &:hover {
            background: #FFFFFF;
            color: #282828;
            border: 2px solid #282828;
        }
    }
}

@media (max-width: 1200px) {
    .basket {
        padding: 95px 160px 130px 160px;
    }
    .emptyBasket {
        padding: 120px 0;
    }
}

@media (max-width: 768px) {
    .basket {
        padding: 60px 20px 80px 20px;
        &__item {
            &-wrapper {
                grid-template-columns: 290px 102px 120px 32px;
            }
            &-pizza {
                grid-template-columns: 80px 179px;
            }
            &-title {
                font-size: 20px;
            }
            &-type {
                font-size: 16px;
            }
            &-price {
                font-size: 20px;
            }
        }
    }
    .emptyBasket {
        padding: 60px 0;
    }
}

@media (max-width: 576px) {
    .basket {
        padding: 35px 10px 60px 10px;
        &__header {
            &-title {
                font-size: 27px;
                line-height: 27px; 
                img {
                    margin-right: 7px;
                    width: 25px;
                    height: 25px;
                }
            }
        }
        &__item {
            margin-top: 20px;
            &-wrapper {
                padding-top: 20px;
                grid-template-columns: 260px 90px 100px 32px;
            }
            &-pizza {
                grid-template-columns: 80px 160px;
            }
            &-title {
                font-size: 20px;
            }
            &-type {
                font-size: 16px;
            }
            &-number {
                font-size: 20px;
            }
            &-amount {
                width: 90px;
            }
            &-price {
                font-size: 20px;
            }
        }
    }
    .emptyBasket {
        padding: 35px 0;
    }
}

@media (max-width: 393px) {
    .basket {
        padding: 20px 10px 45px 10px;
        &__header {
            &-clearBasketButton {
                width: 80px;
                height: 25px;
                font-size: 14px;
                line-height: 14px;
                img {
                    margin: 0 0px 5px 0;
                }
            }
        }
        &__item {
            margin-top: 15px;
            &-wrapper {
                padding-top: 15px;
                grid-template-columns: 140px 72px 71px 25px;
            }
            &-pizza {
                grid-template-columns: 30px 108px;
                column-gap: 2px;
                img {
                    width: 30px;
                    height: 30px;
                }
            }
            &-title {
                font-size: 18px;
                line-height: 15px;
            }
            &-type {
                font-size: 15px;
                line-height: 18px;
            }
            &-amount {
                width: 70px;
            }
            &-signButton {
                width: 25px;
                height: 25px;
            }
            &-number {
                font-size: 17px;
            }
            &-price {
                font-size: 16px;
            }
            &-remove {
                width: 25px;
                height: 25px;
            }
        }
        &__total {
            margin-top: 20px;
            &-amountOfPizzas {
                font-size: 17px;
                line-height: 17px;
                letter-spacing: 0;
            }
            &-totalPrice {
                font-size: 17px;
                line-height: 17px;
                letter-spacing: 0;
            }
        }
        &__buttons {
            margin-top: 30px;
            &-back {
                width: 150px;
                height: 46px;
                font-size: 14px;
                line-height: 14px;
            }
            &-pay {
                width: 150px;
                height: 46px;
                font-size: 14px;
                line-height: 14px;
            }
        }
    }
    .emptyBasket {
        padding: 20px 0;
        &__title {
            font-size: 24px;
        }
        &__prompt {
            font-size: 14px;
        }
        &__img {
            width: 250px;
            height: 205px;
            margin-top: 23px;
        }
        &__backButton {
            margin-top: 40px;
            line-height: 16px;
        }
    }
}
